import React, { useState, useEffect, createContext } from "react";

import { useTranslation } from "next-i18next";

import {
  Modal,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "@heroui/modal";
import { Input } from "@heroui/input";
import { Textarea } from "@heroui/input";
import { Button } from "@heroui/button";

type MessageModalProps = {
  isOpen: boolean;
  setIsOpen: (value: boolean) => void;
};

export default function MessageModal(props: MessageModalProps) {
  const { t, i18n } = useTranslation("common");

  const [name, setName] = useState("");
  const [message, setMessage] = useState("");

  const [error, setError] = useState("");
  const [isSuccess, setIsSuccess] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState(null);

  const clearForm = () => {
    setError("");
    setIsSuccess(false);
    setIsLoading(false);
    setData(null);
    setName("");
    setMessage("");
  };

  return (
    <Modal
      className="font-CaslonPro text-mainTextColor"
      isOpen={props.isOpen}
      onClose={() => props.setIsOpen(false)}
      radius="none"
    >
      <ModalContent>
        <ModalHeader className="flex flex-col gap-1">
          {t("message_us.leave_a_message")}
        </ModalHeader>
        <ModalBody>
          <Input
            classNames={{
              base: "py-6 text-mainTextColor",
              input: "text-mainTextColor",
              inputWrapper: "border-current",
              description: "autofill:!text-mainTextColor",
            }}
            color="primary"
            type="name"
            variant="underlined"
            labelPlacement="outside"
            label={
              <span className="text-mainTextColor">
                {t("message_us.name_label")}
              </span>
            }
            placeholder={t("message_us.name_placeholder")}
            value={name}
            onChange={(e) => setName(e.target.value)}
            disableAnimation={true}
          />
          <Textarea
            classNames={{
              base: "py-6 text-mainTextColor",
              description: "text-mainTextColor",
              inputWrapper: "border-current",
            }}
            color="primary"
            radius="none"
            labelPlacement="outside"
            variant="underlined"
            label={
              <span className="text-mainTextColor">
                {t("message_us.leave_a_message_label")}
              </span>
            }
            placeholder={t("message_us.leave_a_message_placeholder")}
            value={message}
            onValueChange={setMessage}
          />
          <div>{error}</div>
          <div>{isSuccess ? t("message_us.success_message") : null}</div>
        </ModalBody>
        <ModalFooter>
          <Button
            radius="none"
            color="primary"
            onPress={() => {
              if (!name) {
                setError(t("rsvp.name_error_message"));
                return;
              }
              setIsLoading(true);
              fetch("/api/rsvp", {
                method: "POST",
                body: JSON.stringify({
                  language:
                    navigator.languages != undefined
                      ? navigator.languages[0]
                      : navigator.language,
                  sender: name,
                  message: message,
                }),
              })
                .then((res) => res.json())
                .then((data) => {
                  setData(data);
                  setIsLoading(false);
                  setIsSuccess(true);
                  setTimeout(() => {
                    clearForm();
                    props.setIsOpen(false);
                  }, 2000);
                })
                .catch((e) => setError(e));
            }}
          >
            {t("message_us.send")}
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
}
