import React from "react";
import Countdown from "react-countdown";

import { useTranslation } from "next-i18next";

type CountdownRendererType = {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
  completed: boolean;
};

function CountdownRenderer(props: CountdownRendererType) {
  const { t, i18n } = useTranslation("common");

  const zeroPad = (num: number, places: number) =>
    String(num).padStart(places, "0");

  if (props.completed) {
    // Render a completed state
    return null;
  } else {
    // Render a countdown
    return (
      <div className="m-auto text-center font-CaslonPro text-mainTextColor whitespace-pre-line">
        <h2 className="pb-4">{t("home.countdown.title")}</h2>
        <h2 className="pb-8">{t("home.countdown.subtitle")}</h2>
        <div className="flex align-middle justify-center gap-10">
          <div className="flex flex-col">
            <div className="text-[3rem] md:text-[5rem]">{props.days}</div>
            <div>{t("home.countdown.days")}</div>
          </div>
          <div className="flex flex-col">
            <div className="text-[3rem] md:text-[5rem]">
              {zeroPad(props.hours, 2)}
            </div>
            <div>{t("home.countdown.hours")}</div>
          </div>
          <div className="flex flex-col">
            <div className="text-[3rem] md:text-[5rem]">
              {zeroPad(props.minutes, 2)}
            </div>
            <div>{t("home.countdown.minutes")}</div>
          </div>
        </div>
        <h2 className="p-8">{t("home.countdown.footer")}</h2>
      </div>
    );
  }
}

export default function IndexWelcome({}) {
  const { t, i18n } = useTranslation("common");

  const weddingDate = new Date(Date.UTC(2025, 9 - 1, 20, 21, 30));

  return (
    <div id="welcome" className="flex flex-col h-screen bg-white">
      <div className="m-auto">
        <h1 className="text-center text-[4rem] md:text-[6rem] font-FrancieScript text-mainTextColor">
          {t("home.welcome_title")}!
        </h1>
        <h2 className="p-16 max-w-full lg:max-w-screen-lg lg:mx-auto text-center text-[1rem] md:text-[1.5rem] text-mainTextColor font-CaslonPro whitespace-pre-line">
          {t("home.welcome_text")}
        </h2>
        <Countdown
          date={weddingDate}
          renderer={(props) => (
            <CountdownRenderer
              completed={props.completed}
              days={props.days}
              hours={props.hours}
              minutes={props.minutes}
              seconds={props.seconds}
            />
          )}
        />
      </div>
    </div>
  );
}
